import classNames from 'classnames';
import React from 'react';
import { Template } from '../../../types/template';
import PreviewTemplate from './PreviewTemplate';
import { TemplateStatusBadge } from './TemplateStatusBadge';

interface TemplateDetailsProps {
    template: Template;
    variant?: 'card' | 'horizontal';
    showActions?: boolean;
    actions?: React.ReactNode;
}

export const TemplateDetails: React.FC<TemplateDetailsProps> = ({ template, variant = 'card', showActions = false, actions }) => {
    const templateType = (template as any).templateType || template.templateType;
    const templateText = template.metadata?.data || template.components?.find((c) => c.type === 'BODY')?.text;

    if (variant === 'horizontal') {
        return (
            <div className="bg-linear-to-br from-white to-gray-50 border border-gray-200 rounded-2xl overflow-hidden shadow-sm">
                <div className="flex flex-col sm:flex-row">
                    <div className="sm:w-40 shrink-0 bg-linear-to-br from-gray-100 to-gray-50">
                        <div className="aspect-video sm:aspect-square h-full">
                            <PreviewTemplate template={template} />
                        </div>
                    </div>

                    <div className="flex-1 p-4 min-w-0 overflow-hidden">
                        <div className="flex items-start gap-3">
                            <div className="min-w-0 shrink">
                                <h3 className="text-base font-semibold text-gray-900 truncate">{template.displayName}</h3>
                                <p className="text-xs text-gray-400 truncate mt-0.5">{template.name}</p>
                            </div>
                            <div className="flex flex-wrap gap-1.5 ml-auto shrink-0">
                                <TemplateBadge label={template.type} variant="gray" />
                                {templateType && <TemplateBadge label={templateType} variant="blue" />}
                                {!!template.parameterCount && (
                                    <TemplateBadge
                                        label={`${template.parameterCount} ${template.parameterCount === 1 ? 'param' : 'params'}`}
                                        variant="purple"
                                    />
                                )}
                                {template.category && <TemplateBadge label={template.category} variant="indigo" />}
                                {template.language && <TemplateBadge label={template.language.toUpperCase()} variant="teal" />}
                                {template.qualityRating && template.qualityRating !== 'UNKNOWN' && (
                                    <TemplateBadge
                                        label={template.qualityRating}
                                        variant={
                                            template.qualityRating === 'HIGH'
                                                ? 'green'
                                                : template.qualityRating === 'MEDIUM'
                                                  ? 'yellow'
                                                  : 'red'
                                        }
                                    />
                                )}
                            </div>
                        </div>

                        {templateText && (
                            <div className="mt-3 p-2.5 bg-gray-50 border border-gray-100 rounded-lg">
                                <p className="text-sm text-gray-700 leading-relaxed line-clamp-2">{templateText}</p>
                            </div>
                        )}
                    </div>
                </div>
            </div>
        );
    }

    return (
        <div className="space-y-3">
            <div className="flex items-start justify-between">
                <div className="flex-1 min-w-0">
                    <h3 className="font-semibold text-gray-900 text-base truncate mb-2">{template.displayName}</h3>
                    <div className="flex flex-wrap gap-1.5">
                        <TemplateStatusBadge status={template.status} />
                        <TemplateBadge label={template.type} variant="gray" />
                        {templateType && <TemplateBadge label={templateType} variant="blue" />}
                        {!!template.parameterCount && (
                            <TemplateBadge
                                label={`${template.parameterCount} ${template.parameterCount === 1 ? 'param' : 'params'}`}
                                variant="purple"
                            />
                        )}
                        {template.qualityRating && template.qualityRating !== 'UNKNOWN' && (
                            <TemplateBadge
                                label={template.qualityRating}
                                variant={
                                    template.qualityRating === 'HIGH' ? 'green' : template.qualityRating === 'MEDIUM' ? 'yellow' : 'red'
                                }
                            />
                        )}
                    </div>
                </div>
                {showActions && actions && <div className="ml-3 shrink-0">{actions}</div>}
            </div>

            <div className="flex items-center text-xs text-gray-500 gap-4">
                <div className="flex items-center gap-1">
                    <svg className="w-3.5 h-3.5" fill="currentColor" viewBox="0 0 24 24">
                        <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8zm.5-13H11v6l5.25 3.15.75-1.23-4.5-2.67z" />
                    </svg>
                    <span>{new Date(template.createdAt).toLocaleDateString()}</span>
                </div>
                {!!template.usageCount && (
                    <div className="flex items-center gap-1">
                        <svg className="w-3.5 h-3.5" fill="currentColor" viewBox="0 0 24 24">
                            <path d="M16 11c1.66 0 2.99-1.34 2.99-3S17.66 5 16 5c-1.66 0-3 1.34-3 3s1.34 3 3 3zm-8 0c1.66 0 2.99-1.34 2.99-3S9.66 5 8 5C6.34 5 5 6.34 5 8s1.34 3 3 3zm0 2c-2.33 0-7 1.17-7 3.5V19h14v-2.5c0-2.33-4.67-3.5-7-3.5zm8 0c-.29 0-.62.02-.97.05 1.16.84 1.97 1.97 1.97 3.45V19h6v-2.5c0-2.33-4.67-3.5-7-3.5z" />
                        </svg>
                        <span>{template.usageCount} uses</span>
                    </div>
                )}
            </div>

            {templateText && <p className="text-xs text-gray-600 line-clamp-2 leading-relaxed">{templateText}</p>}

            {template.status === 'rejected' && template.rejectionReason && (
                <div className="bg-red-50 border border-red-200 rounded-lg p-2.5">
                    <p className="text-xs font-semibold text-red-800">Rejected</p>
                    <p className="text-xs text-red-700 mt-0.5">{template.rejectionReason}</p>
                </div>
            )}
        </div>
    );
};

type BadgeVariant = 'gray' | 'blue' | 'purple' | 'indigo' | 'teal' | 'green' | 'yellow' | 'red';

const badgeVariantClasses: Record<BadgeVariant, string> = {
    gray: 'text-gray-600 bg-gray-100',
    blue: 'text-blue-600 bg-blue-50',
    purple: 'text-purple-600 bg-purple-50',
    indigo: 'text-indigo-600 bg-indigo-50',
    teal: 'text-teal-600 bg-teal-50',
    green: 'text-green-700 bg-green-50',
    yellow: 'text-yellow-700 bg-yellow-50',
    red: 'text-red-700 bg-red-50',
};

const TemplateBadge: React.FC<{ label: string; variant: BadgeVariant }> = ({ label, variant }) => (
    <span className={classNames('text-xs px-2 py-1 rounded-md font-medium uppercase', badgeVariantClasses[variant])}>{label}</span>
);
