import { PlayIcon } from 'ether-ui/icons';
import React, { useRef, useState } from 'react';

const VideoPreview: React.FC<{ videoUrl: string }> = ({ videoUrl }) => {
    const [isPlaying, setIsPlaying] = useState(false);
    const videoRef = useRef<HTMLVideoElement>(null);

    const togglePlay = () => {
        if (videoRef.current) {
            if (isPlaying) {
                videoRef.current.pause();
            } else {
                videoRef.current.play();
            }
            setIsPlaying(!isPlaying);
        }
    };

    return (
        <div className="relative w-full h-48 bg-black group">
            <video
                ref={videoRef}
                src={videoUrl}
                className="w-full h-full object-cover"
                onEnded={() => setIsPlaying(false)}
                onClick={togglePlay}
            />
            {!isPlaying && (
                <button
                    onClick={togglePlay}
                    className="absolute inset-0 flex items-center justify-center bg-black bg-opacity-30 group-hover:bg-opacity-40 transition-all"
                >
                    <div className="w-16 h-16 rounded-full bg-white bg-opacity-90 flex items-center justify-center shadow-lg group-hover:scale-110 transition-transform">
                        <PlayIcon className="w-8 h-8 text-gray-800 ml-1" />
                    </div>
                </button>
            )}
        </div>
    );
};

export default VideoPreview;
