import { DownloadIcon } from 'ether-ui/icons';
import { Template } from '../../../types/template';
import NoPreviewPlaceholder from './NoPreviewPlaceholder';
import VideoPreview from './VideoPreview';

function PreviewTemplate({ template }: { template: Template }) {
    const mediaUrl = getMediaUrl(template);
    const templateType = (template as any).templateType || template.templateType;

    const handleMediaDownload = () => {
        if (mediaUrl) {
            const fileName = getFileName(mediaUrl, templateType, template.displayName);
            handleDownload(mediaUrl, fileName);
        }
    };

    return (
        <>
            {templateType === 'IMAGE' && mediaUrl ? (
                <div className="relative w-full h-48 bg-gray-100 group">
                    <img src={mediaUrl} alt={template.displayName} className="w-full h-full object-cover" />
                    <button
                        onClick={handleMediaDownload}
                        className="absolute top-2 right-2 w-8 h-8 rounded-full bg-white bg-opacity-90 hover:bg-opacity-100 flex items-center justify-center shadow-md opacity-0 group-hover:opacity-100 transition-opacity cursor-pointer"
                        title="Download Image"
                    >
                        <DownloadIcon className="w-4 h-4 text-gray-700 cursor-pointer" />
                    </button>
                </div>
            ) : templateType === 'VIDEO' && mediaUrl ? (
                <div className="relative">
                    <VideoPreview videoUrl={mediaUrl} />
                    <button
                        onClick={handleMediaDownload}
                        className="absolute top-2 right-2 w-8 h-8 rounded-full bg-white bg-opacity-90 hover:bg-opacity-100 flex items-center justify-center shadow-md transition-all z-10 cursor-pointer"
                        title="Download Video"
                    >
                        <DownloadIcon className="w-4 h-4 text-gray-700 cursor-pointer" />
                    </button>
                </div>
            ) : templateType === 'DOCUMENT' && mediaUrl ? (
                <div className="relative">
                    <NoPreviewPlaceholder templateType={templateType} />
                    <button
                        onClick={handleMediaDownload}
                        className="absolute top-2 right-2 w-8 h-8 rounded-full bg-white bg-opacity-90 hover:bg-opacity-100 flex items-center justify-center shadow-md transition-all cursor-pointer"
                        title="Download Document"
                    >
                        <DownloadIcon className="w-4 h-4 text-gray-700" />
                    </button>
                </div>
            ) : (
                <NoPreviewPlaceholder templateType={templateType || 'TEXT'} />
            )}
        </>
    );
}

export default PreviewTemplate;

const getMediaUrl = (template: Template): string | null => {
    if (template.metadata?.mediaUrl) return template.metadata.mediaUrl;

    if (template.metadata?.meta) {
        try {
            const meta = typeof template.metadata.meta === 'string' ? JSON.parse(template.metadata.meta) : template.metadata.meta;
            return meta?.mediaUrl || null;
        } catch {
            return null;
        }
    }

    return null;
};

const getFileName = (url: string, templateType: string, templateName: string): string => {
    try {
        const urlObj = new URL(url);
        const pathParts = urlObj.pathname.split('/');
        const fileName = pathParts[pathParts.length - 1];
        if (fileName && fileName.includes('.')) {
            return fileName;
        }
    } catch {}

    const extension = templateType === 'VIDEO' ? 'mp4' : templateType === 'IMAGE' ? 'jpg' : templateType === 'DOCUMENT' ? 'pdf' : 'file';
    return `${templateName.replace(/[^a-z0-9]/gi, '_').toLowerCase()}.${extension}`;
};

const handleDownload = async (url: string, fileName: string) => {
    try {
        const response = await fetch(url);
        const blob = await response.blob();
        const blobUrl = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = blobUrl;
        link.download = fileName;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        window.URL.revokeObjectURL(blobUrl);
    } catch (error) {
        window.open(url, '_blank');
    }
};
