const NoPreviewPlaceholder: React.FC<{ templateType: string }> = ({ templateType }) => {
    const getIcon = () => {
        switch (templateType) {
            case 'DOCUMENT':
                return (
                    <svg className="w-16 h-16" fill="currentColor" viewBox="0 0 24 24">
                        <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8l-6-6zm4 18H6V4h7v5h5v11z" />
                        <path d="M8 12h8v2H8v-2zm0 4h8v2H8v-2z" />
                    </svg>
                );
            case 'LOCATION':
                return (
                    <svg className="w-16 h-16" fill="currentColor" viewBox="0 0 24 24">
                        <path d="M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7-7.75 7-13c0-3.87-3.13-7-7-7zm0 9.5a2.5 2.5 0 0 1 0-5 2.5 2.5 0 0 1 0 5z" />
                    </svg>
                );
            case 'TEXT':
                return (
                    <svg className="w-16 h-16" fill="currentColor" viewBox="0 0 24 24">
                        <path d="M4 6h16v2H4V6zm0 5h16v2H4v-2zm0 5h16v2H4v-2z" />
                    </svg>
                );
            default:
                return (
                    <svg className="w-16 h-16" fill="currentColor" viewBox="0 0 24 24">
                        <path d="M19 3H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm0 16H5V5h14v14z" />
                    </svg>
                );
        }
    };

    return (
        <div className="w-full h-48 bg-linear-to-br from-gray-100 to-gray-200 flex flex-col items-center justify-center text-gray-400">
            {getIcon()}
            <p className="mt-3 text-sm font-medium">{templateType}</p>
            <p className="text-xs">No Preview Available</p>
        </div>
    );
};

export default NoPreviewPlaceholder;
